/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from 'react'
import Helmet from 'react-helmet'
import { useStaticQuery, graphql } from 'gatsby'
import { Site } from '../graphql-types'

type QueryProps = {
  site: Site
}

export const SEO = () => {
  const { site } = useStaticQuery<QueryProps>(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
          }
        }
      }
    `,
  )

  const { title, description, author } = site.siteMetadata!

  return (
    <Helmet
      title={title!}
      htmlAttributes={{
        lang: 'zh',
      }}
      titleTemplate={`%s | ${title!}`}
      meta={[
        {
          name: `description`,
          content: description!,
        },
        {
          property: `og:title`,
          content: title!,
        },
        {
          property: `og:description`,
          content: description!,
        },
        {
          property: `og:type`,
          content: `website`,
        },
        {
          name: `twitter:card`,
          content: `summary`,
        },
        {
          name: `twitter:creator`,
          content: author!,
        },
        {
          name: `twitter:title`,
          content: title!,
        },
        {
          name: `twitter:description`,
          content: description!,
        },
      ]}
    />
  )
}
